import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/Home'
import Detail from '../pages/Detail'
import Search from '../pages/Search'

Vue.use(Router);

function createRouter() {
  const routes = [
    {
      path: '/',
      name: 'home',
      component: Home,
      redirect: '/mine',
      children: [
        {
          path: 'mine',
          name: 'mine',
          component: () => import('../components/Mine')
        },
        {
          path: 'project',
          name: 'project',
          component: () => import('../components/Project')
        },
        {
          path: 'dynamic',
          name: 'dynamic',
          component: () => import('../components/Dynamic')
        },
        {
          path: 'notice',
          name: 'notice',
          component: () => import('../components/Notice')
        },
        {
          path: 'team',
          name: 'team',
          component: () => import('../components/Team')
        }
      ]
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail,
      redirect: {
        name: 'taskDetail'
      },
      children: [
        {
          path: 'taskDetail/:pId?:mId?:tId?',
          name: 'taskDetail',
          component: () => import('../components/TaskDetail')
        },
        {
          path: 'projectDetail/:id?',
          name: 'projectDetail',
          component: () => import('../components/ProjectDetail')
        },
        {
          path: 'teamDetail/:id?',
          name: 'teamDetail',
          component: () => import('../components/TeamDetail')
        }
      ]
    },
    {
      path: '/search/:type?',
      name: 'search',
      component: Search
    }
  ];

  const router = new Router({
    mode: 'history',
    linkExactActiveClass: 'linkActiveClass',
    routes
  });

  return router;
}

export default createRouter;